<template>
  <b-container>
    <ul>
      <li @click="$_setAbout(false)" :class="{ active: !isAbout }">Work</li>
      <li @click="$_setAbout(true)" :class="{ active: isAbout }">About</li>
    </ul>
  </b-container>
</template>

<script>
export default {
  name: "TheMenu",
  props: {
    isAbout: Boolean
  },
  methods: {
    $_setAbout(newVal) {
      this.$emit("aboutSet", newVal);
    }
  }
};
</script>

