li {
  transition: all 0.5s;
  cursor: pointer;
  font-size: 2rem;
  letter-spacing: 0.5rem;
}

li:hover {
  transform: initial;
}

.active {
  letter-spacing: 1rem;
  font-weight: 700;
}
